<template>
  <Form ref="loginForm" :model="form" :rules="rules"   @keydown.enter.native="handleSubmit">
    <FormItem prop="username">
      <Input v-model="form.username" placeholder="">
        <span slot="prepend">
          <Icon :size="16" type="ios-person"></Icon>
        </span>
      </Input>
    </FormItem>
    <FormItem prop="password">
      <Input type="password" v-model="form.password" placeholder="">
        <span slot="prepend">
          <Icon :size="14" type="md-lock"></Icon>
        </span>
      </Input>
    </FormItem>
    <FormItem v-if="secoundAuth" prop="dynamic">
      <Input type="text" :maxlength=6 v-model="form.dynamic" autofocus="autofocus" placeholder="">
        <span slot="prepend">
          <Icon :size="14" type="md-key"></Icon>
        </span>
      </Input>
    </FormItem>
    <FormItem>
      <Button @click="handleSubmit" type="primary" long></Button>
    </FormItem>
  </Form>
</template>
<script>
export default {
  name: 'LoginForm',
  props: {
    secoundAuth: {
      type: Boolean,
      default: false
    },
    userNameRules: {
      type: Array,
      default: () => {
        return [{ required: true, message: '', trigger: 'blur' }]
      }
    },
    passwordRules: {
      type: Array,
      default: () => {
        return [
          { required: true, message: '', trigger: 'blur' },
          {type: 'string', min: 6, message: '6', trigger: 'blur'}
        ]
      }
    },
    dynamicRules: {
      type: Array,
      default: () => {
        return [
          { required: true, message: '', trigger: 'blur' },
          { type: 'string', min: 6, max: 6, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  data () {
    return {
      form: {
        username: '',
        password: '',
        dynamic:''
      }
    }
  },
  computed: {
    rules () {
      return {
        username: this.userNameRules,
        password: this.passwordRules,
        dynamic: this.dynamicRules
      }
    }
  },
  methods: {
    handleSubmit () {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.$emit('on-success-valid', {
            username: this.form.username,
            password: this.form.password,
            dynamic: this.form.dynamic
          })
        }
      })
    }
  }
}
</script>
