<template>
  <!-- <Row style="padding:10px;">
    <Col span="14" offset="0"> -->
      <!-- <Card> -->
        <!-- <p slot="title"></p> -->
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
          <!-- <FormItem label="" prop="username">
            <Input readonly v-model="formValidate.username" placeholder=""></Input>
          </FormItem> -->
          <template v-if="username">
            <FormItem label="" prop="username">
              <Input  v-model="formValidate.username" disabled readonly  placeholder=""></Input>
            </FormItem>
          </template>
           <template v-else>
              <FormItem label="" prop="username">
            <Input readonly v-model="formValidate.username" placeholder=""></Input>
          </FormItem>
          </template>

          <FormItem label="" prop="nickname">
            <Input v-model="formValidate.nickname" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="password">
            <Input type ="password" v-model="formValidate.password" placeholder="8"></Input>
          </FormItem>
          <FormItem label="" prop="department">
            <Input v-model="formValidate.department" placeholder="Enter your department"></Input>
          </FormItem>
          <FormItem label="" prop="tel">
            <Input v-model="formValidate.tel" placeholder="Enter your tel"></Input>
          </FormItem>
          <FormItem label="" prop="wechat">
            <Input v-model="formValidate.wechat" placeholder="Enter your wechat"></Input>
          </FormItem>
          <FormItem label="" prop="no">
            <Input v-model="formValidate.no" placeholder=""></Input>
          </FormItem>
          <template v-if="mail">
            <FormItem label="" prop="email">
              <Input readonly v-model="formValidate.email" disabled  placeholder="Enter your e-mail "></Input>
            </FormItem>
          </template>
           <template v-else>
             <FormItem label="" prop="email">
              <Input  v-model="formValidate.email"  placeholder="Enter your e-mail "></Input>
            </FormItem>
          </template>
          <!-- <FormItem label="" prop="sex">
            <RadioGroup v-model="formValidate.sex">
                <Radio label=""></Radio>
                <Radio label=""></Radio>
            </RadioGroup>
          </FormItem> -->
          <FormItem label="" prop="desc">
            <Input v-model="formValidate.desc" type="textarea" :autosize="{minRows: 5,maxRows: 8}" placeholder="Enter something..."></Input>
          </FormItem>
          <FormItem>
            <Button type="primary" :loading="loading" @click="handleSubmit('formValidate')"></Button>
            <Button @click="handleReset('formValidate')" style="margin-left: 10px"></Button>
          </FormItem>
        </Form>
      <!-- </Card> -->
    <!-- </Col span="25">
  </Row> -->
</template>
<script>
export default {
  name: 'RegisterForm',
  props: {
    mail: {
      type: String,
    },
    username: {
      type: String
    }
  },
  data () {
    return {
      loading: false,
      formValidate: {
        username: '',
        nickname: '18888888888',
        password: '18888888888',
        department: '18888888888',
        tel: '18888888888',
        wechat: '18888888888',
        no: '18888888888',
        email: ''
        // sex: "",
        // desc: ""
      },
      ruleValidate: {
        username: [
          {
            required: true,
            message: 'The username cannot be empty',
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 3,
            max: 20,
            message: '20',
            trigger: 'blur'
          }
        ],
        nickname: [
          {
            required: true,
            message: 'The nickname cannot be empty',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: 'The password cannot be empty',
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 8,
            message: '8',
            trigger: 'blur'
          }
        ],
        department: [
          {
            required: true,
            message: 'The department cannot be empty',
            trigger: 'blur'
          }
        ],
        tel: [
          {
            required: true,
            message: 'The tel cannot be empty',
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 11,
            max: 11,
            message: '',
            trigger: 'blur'
          }
        ],
        wechat: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        no: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        email: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    mail (value) {
      this.formValidate.email = value
    },
    username (value) {
      this.formValidate.username = value
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$emit('on-submit-success', this.formValidate)
          this.loading = true
          setTimeout(() => {
            this.loading = false
          }, 1000)
        } else {
          this.$Message.error('Validate Fail!')
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    }
  }
}
</script>
