<template>
  <div>
    <Card v-if="!showEdit" dis-hover :bordered="false">
      <Alert show-icon>
        
        <template slot="desc">
          <p>1</p>
          <p>2</p>
          <p>3</p>
        </template>
      </Alert>
      <div class="search-con search-con-top">
        <Button type="primary" class="search-btn" @click="handleAdd"></Button>
        <Input class="search-input" v-model="initPara.searchText" search @on-search="handleSearch" :maxlength='80'
          placeholder="" />

        <Button style="float: right; margin-right: 2px" icon="md-refresh" :loading="i.btnLoading"
          @click="handleRefresh"></Button>

      </div>
      <Table ref="selection" @on-sort-change="handleSort" @on-selection-change="handleSelect" :columns="columns"
        :data="tableData" :loading="i.tableLoading"></Table>

      <div style="margin: 10px; overflow: hidden">
        <div style="float: right;">
          <Page :total="i.pageTotal" :current="initPara.pageNum" :page-size="initPara.pageSize" show-total show-sizer
            show-elevator :page-size-opts="[10, 15, 50, 1000]" @on-change="handlerChangePage"
            @on-page-size-change="handlePageSize" />
        </div>
      </div>
    </Card>

    <notification-edit v-else :formData="theData" :tplList="tplList" :userList="userList" :action="action"
      @save-data="handleSaveData" @edit-close="handleClose" />

  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import { getNoticeTPL } from '@/api/notice/tpl'
import { getChannel, getChannelD, optChannel, getUers } from '@/api/notice/channel'
import notificationEdit from './components/notification-edit'
import formatDate from '@/libs/util.js'
export default {
  components: { notificationEdit },
  data () {
    return {
      userList: [],
      tplList: [],
      showEdit: false,
      action: 'post',
      //
      initPara: {
        pageNum: 1, // 
        pageSize: 10,  // 
        searchText: "",//
        field: "",      //
        order: "descend", //
        searchField: "",  // 
        cache: "no",// 
      },
      filter_map: {},
      initForm: {
        use: "zhulong",
        contact_points: [
          {
            severity_desc: '',
            severity: ['fatal'],
            type: 'alydh',
            show: 'no',
            content: '${msg}',
          },
          // {
          //   severity_desc: ' + ',
          //   severity: ['fatal', 'error'],
          //   type: 'alydx',
          //   show: 'no',
          //   content: '${msg}'
          // },
          // {
          //   tpl_id: '',
          //   severity_desc: ' + ',
          //   severity: ['fatal', 'error'],
          //   type: 'fsapp',
          //   show: 'no',
          //   content: ''
          // },
          {
            severity_desc: ' +  + ',
            severity: ['fatal', 'error', 'warn'],
            type: 'fs',
            show: 'no',
            content: '',
            webhook: '',
            secret: ''
          },
          // {
          //   severity_desc: ' +  +  + ',
          //   severity: ['fatal', 'error', 'warn', 'info'],
          //   type: 'dd',
          //   show: 'no',
          //   content: '',
          //   webhook: '',
          //   secret: ''
          // }
        ],
        custom_items: [{
          severity: ['fatal', 'error', 'warn'],
          type: 'fs',
          content: '',
          webhook: '',
          secret: '',
          index: 1,
          status: 1
        }],
      },
      i: {
        pageTotal: 0, // 
        tableLoading: true,
        btnDisabled: true,
        btnLoading: false
      },
      selectList: [],
      theData: {},
      columns: [
        {
          title: '',
          key: 'name',
          minWidth: 120,
        },
        {
          title: '',
          key: 'updated_by',
          sortable: true,
          minWidth: 105,
          maxWidth: 200
        },
        {
          title: '',
          key: 'updated_at',
          minWidth: 155,
          maxWidth: 250,
          render: (h, params) => {
            return h("div", formatDate(params.row.updated_at));
          },
        },
        {
          title: '',
          key: '',
          minWidth: 120,
          maxWidth: 200,
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.handleEdit(params.row)
                  }
                }
              }, ''
              ),
              h('a', {
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => { this.handleDel(params.row) }
                }
              }, ''
              )
            ])
          }
        }
        //
      ],
      tableData: []
    }
  },
  methods: {
    async handleGetData () {
      this.initPara['filter_map'] = this.filter_map
      let res = await getChannel(this.initPara)
      if (res.data.code === 0) {
        this.tableData = res.data.result.data;
        this.i.pageTotal = res.data.result.count;
      } else {
        this.$Message.error(res.data.message)
      }
      this.i.tableLoading = false
      setTimeout(() => {
        this.i.btnLoading = false
      }, 1000);
    },

    async handleGetInfo (id) {
      let res = await getChannelD(id)
      if (res.data.code == 0) {
        this.theData = res.data.result
      } else (
        this.$Message.error(res.data.message)
      )
    },

    async getTPL () {
      //filter_map: { use: use }
      let res = await getNoticeTPL({ pageNum: 1, pageSize: 10000 })
      if (res.data.code == 0) {
        this.tplList = res.data.result.data
      } else (
        this.$Message.error(res.data.message)
      )
    },

    async handleGetUsers () {
      let res = await getUers({ pageNum: 1, pageSize: 10000 })
      if (res.data.code !== 0) {
        this.$Message.error(res.data.message)
      };
      this.userList = res.data.result.data
    },

    handleClose () {
      this.showEdit = false
      this.handleGetData()
    },

    handleAdd () {
      this.action = 'post'
      this.showEdit = true
      this.theData = Object.assign(this.$options.data().theData, this.initForm)
    },
    handleEdit (paramsRow) {
      this.theData = Object.assign(this.$options.data().theData, this.initForm)
      this.handleGetInfo(paramsRow.id)
      this.getTPL()
      this.action = 'put'
      this.showEdit = true
    },
    // 
    handleSaveData (saveData) {
      optChannel(saveData, this.action).then(res => {
        if (res.data.code == 0) {
          this.handleClose()
          this.$Message.success(res.data.message)
        } else {
          this.$Message.error(res.data.message)
        }
      })
    },
    //
    handleDel (paramsRow) {
      this.$Modal.confirm({
        title: '',
        content: `<p>${paramsRow.name}</p>`,
        onOk: () => {
          setTimeout(() => {
            optChannel({ ids: [paramsRow.id] }, 'delete').then(res => {
              if (res.data.code === 0) {
                this.$Message.success(res.data.message)
                this.handleGetData()
              } else {
                this.$Message.error('')
              }
            })
          }, 1000);
        },
        onCancel: () => { this.$Message.info('~') }
      });
    },
    //
    handleRefresh () {
      this.initPara.pageNum = 1
      this.initPara.cache = 'no'
      this.handleGetData()
    },
    handleSearch () {
      this.initPara.pageNum = 1
      this.handleGetData()
    },
    handleSelect (val) {
      let selectIdList = []
      val.forEach(item => {
        selectIdList.push(item.id)
      })
      this.selectList = selectIdList
    },
    handleSort (column, key, order) {
      this.initPara.order = order === "asc" ? 'ascend' : 'descend'
      this.initPara.field = key
    },
    handlerChangePage (value) {
      this.initPara.pageNum = value
      this.handleGetData()
    },
    handlePageSize (value) {
      this.initPara.pageNum = 1
      this.initPara.pageSize = value
      this.handleGetData()
    },
    //
    ...mapMutations([
      'setShowBusiness'
    ]),

  },
  watch: {
    selectList (val) {
      if (val.length > 0) {
        this.i.btnDisabled = false
      } else {
        this.i.btnDisabled = true
      }
    },
  },
  computed: {
    ...mapState({
      rules: state => state.user.rules,
    })
  },
  created () {
    this.getTPL()
  },
  mounted () {
    this.setShowBusiness(false)
    this.handleGetData()
    this.handleGetUsers()
  },
  beforeRouteLeave (to, from, next) {
    this.setShowBusiness(true)
    next()
  }

  //
}
</script>
<style lang="less" scoped>
.search-con {
  padding: 10px 0;

  .search {
    &-col {
      display: inline-block;
      width: 200px;
    }

    &-input {
      display: inline-block;
      width: 400px;
      margin-left: 5px;
    }

    &-btn {
      display: inline-block;
      margin-left: 2px;
      margin-right: 5px;
    }

    &-add-btn {
      margin-right: 10px;
    }
  }
}
</style>
